.main-navigation__toggle {
  position: absolute;
  top: -38px;
  width: 26px;
  height: 16px;
  background-color: #ffffff;
  border: none;
  cursor: pointer;
  right: 10px;
  padding: 0;

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.main-navigation__list {
  padding: 0;
  margin: 0;

  @media (min-width: $tablet-width) {
    display: flex;
  }

  @media (min-width: $desktop-width) {
    justify-content: flex-end;
  }
}
