.social-list {
  margin: 0;
  // padding: 0;
  display: flex;
  // min-width: 132px;
  gap: 24px;
  flex-wrap: wrap;
  padding: 20px 0;
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
  width: 100%;
  justify-content: center;

  @media (min-width: $tablet-width) {
    padding: 0;
    border: none;
  }
}

.social-list__item {
  list-style-type: none;
}

.social-list__link {
  text-decoration: none;
  color: #000000;
}
