  }
}

.main-navigation--closed .main-navigation__toggle::before {
  content: "";
  position: absolute;
  width: 26px;
  height: 2px;
  right: 0;
  top: 0;
  background-color: #000000;
  box-shadow: 0 6px 0 0 #000000, 0 12px 0 0 #000000;
}

@media (max-width: 767px) {
  .main-navigation--opened .main-navigation__wrapper {
    position: absolute;
    background-color: #ffffff;
    width: 100%;
  }
  .main-navigation--closed .site-list {
    display: none;
  }
}
.main-navigation--closed .main-navigation__toggle:active::before,
.main-navigation--closed .main-navigation__toggle:hover::before {
  opacity: 0.6;
}

.main-navigation--opened .main-navigation__toggle {
  right: 13px;
  top: -38px;
}
.main-navigation--opened .main-navigation__toggle::before, .main-navigation--opened .main-navigation__toggle::after {
  content: "";
  position: absolute;
  width: 26px;
  height: 2px;
  background-color: #000000;
  left: 1px;
}
.main-navigation--opened .main-navigation__toggle::before {
  transform: rotate(45deg);
}
.main-navigation--opened .main-navigation__toggle::after {
  transform: rotate(-45deg);
}

.main-navigation__toggle {
  position: absolute;
  top: -38px;
  width: 26px;
  height: 16px;
  background-color: #ffffff;
  border: none;
  cursor: pointer;
  right: 10px;
  padding: 0;
}
